import Vue from 'vue'
import VueRouter from 'vue-router'
import ProductWithDesign from "../views/ProductWithDesign";
import Promociones from "../views/Promociones";
import ProductWithCards from "../views/ProductWithCards";


Vue.use(VueRouter)

  const routes = [
    {
      path: '/productos-con-diseño',
      name: 'productosConDiseño',
      component: ProductWithDesign
    },
    {
      path: '/promociones',
      name: 'Promociones',
      component: Promociones
    },
    {
      path: '/productos-con-cards',
      name: 'ProductosConCard',
      component: ProductWithCards
    }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
