<template lang="pug">
    div.flex.justify-center.items-center.flex-col(class="lg:justify-start")
        h3.text-xl(class="lg:my-2") CATEGORÍA
        p.border-2.border-blue-400.rounded-full.cursor-pointer(v-show="showall === false" @click="showall = true" class="lg:hidden lg:border-0 w-10/12") {{elegido}}
            i.fal.fa-chevron-down.ml-4
        ul.border-2.border-blue-400.rounded-lg.flex.flex-col(v-show="showall === true" class="lg:border-0 w-10/12 lg:w-full")
            li.text-blue-400.order-1(@click="clicked('d-0')" id="d-0") Todos (x)
            li.order-2(@click="clicked('d-1')" id="d-1") Cat1 (x)
            li.order-2(@click="clicked('d-2')" id="d-2") Cat2 (x)
            li.order-2(@click="clicked('d-3')" id="d-3") Cat3 (x)
            li.order-2(@click="clicked('d-4')" id="d-4") Cat4 (x)
            li.order-2(@click="clicked('d-5')" id="d-5") Cat5 (x)
            li.order-2(@click="clicked('d-6')" id="d-6") Cat6 (x)
            li.order-2(@click="clicked('d-7')" id="d-7") Cat7 (x)
            li.order-2(@click="clicked('d-8')" id="d-8") Cat8 (x)
            li.order-2(@click="clicked('d-9')" id="d-9") Cat9 (x)

</template>

<script>
    export default {
        name: "DesignCategories",
        data(){
            return {
                anterior: 'd-0',
                elegido: "Todos (x)",
                showall: (window.innerWidth >= 1024)
            }
        },
        methods:{
            clicked(idClickeado){
                let elementoAnterior = document.getElementById(this.anterior);
                elementoAnterior.classList.remove("text-blue-400");
                elementoAnterior.classList.add("text-black");
                let clickedElement = document.getElementById(idClickeado);
                clickedElement.classList.remove("text-black");
                clickedElement.classList.add("text-blue-400");
                this.anterior = idClickeado;
                this.elegido = clickedElement.innerHTML;
                if (window.innerWidth < 1024){
                    elementoAnterior.classList.remove("order-1");
                    elementoAnterior.classList.add("order-2");
                    clickedElement.classList.remove("order-2");
                    clickedElement.classList.add("order-1");
                    this.showall = false;
                }
            }
        }
    }
</script>

<style lang="sass" scoped>
    ul
        li
           cursor: pointer
</style>