<template lang="pug">
    .icons.grid.grid-cols-3.gap-4.w-full.mt-8.bg-gray-300(class="lg:grid-cols-6 lg:gap-0")
        .icono.text-blue-400.border-b-2.border_blue_bottom(@click="clicked('i-p-1')" id="i-p-1")
            p.text-xl icono
            span.text-sm Todos
        .icono(@click="clicked('i-p-2')" class="lg:border-l lg:border-gray-400 " id="i-p-2")
            p.text-xl icono
            span.text-sm Photobooks
        .icono(@click="clicked('i-p-3')" class="lg:border-l lg:border-gray-400 " id="i-p-3")
            p.text-xl icono
            span.text-sm Foto Album
        .icono(@click="clicked('i-p-4')" class="lg:border-l lg:border-gray-400 " id="i-p-4")
            p.text-xl icono
            span.text-sm Home Decor
        .icono(@click="clicked('i-p-5')" class="lg:border-l lg:border-gray-400 " id="i-p-5")
            p.text-xl icono
            span.text-sm Impresiones
        .icono(@click="clicked('i-p-6')" class="lg:border-l lg:border-gray-400 " id="i-p-6")
            p.text-xl icono
            span.text-sm Gift Box
</template>

<script>
    export default {
        name: "IconPromos",
        data(){
            return {
                anterior: 'i-p-1',
                showall: (window.innerWidth >= 1024)
            }
        },
        methods:{
            clicked(idClickeado){
                let elementoAnterior = document.getElementById(this.anterior);
                elementoAnterior.classList.remove("text-blue-400");
                elementoAnterior.classList.remove("border-b-2");
                elementoAnterior.classList.remove("border_blue_bottom");
                elementoAnterior.classList.add("text-black");
                let clickedElement = document.getElementById(idClickeado);
                clickedElement.classList.remove("text-black");
                clickedElement.classList.add("text-blue-400");
                clickedElement.classList.add("border-b-2");
                clickedElement.classList.add("border_blue_bottom");
                this.anterior = idClickeado;
            }
        }
    }
</script>

<style lang="sass" scoped>
    .border_blue_bottom
        border-bottom-color: deepskyblue
</style>